:root {
    --primary-color: #e22b2b;
    --secondary-color: #db7070;
    --tertiary-color: #DDA0DD;
    --background-light: #F0E6FA;
    --text-dark: #333333;
    --text-light: #000000;
    --border-color: #C6A4ED;
}

body {
    font-family: 'Roboto Mono', monospace;
    background-color: var(--background-light);
    color: var(--text-dark);
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
    padding: 20px;
    box-sizing: border-box;
}

.pokedex {
    background: linear-gradient(145deg, var(--secondary-color),
     var(--primary-color));
    border-radius: 20px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25),
     0 0 15px rgba(138, 43, 226, 0.5);
    border: 2px solid var(--border-color);
    width: 100%;
    max-width: 320px;
    padding: 25px;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}

.pokedex::before {
    content: '';
    position: absolute;
    top: -50px;
    left: -50px;
    width: 200px;
    height: 200px;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.15) 0%,
     transparent 70%);
    animation: pulse-light 5s infinite alternate;
    z-index: 0;
    pointer-events: none;
}

@keyframes pulse-light {
    0% { transform: scale(0.8) translate(0, 0); opacity: 0.6; }
    100% { transform: scale(1.2) translate(20px, 20px); opacity: 0.8; }
}

.pokedex-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 5px dashed var(--border-color);
    padding-bottom: 15px;
    margin-bottom: 25px;
    position: relative;
    z-index: 1;
}

.pokedex-header h1 { margin: 0; font-size: 1.8em; color: var(--text-light); text-shadow: 2px 2px 5px rgb(0, 0, 0);} 

.pokedex-brand { font-weight: bold; font-size: 0.9em; background-color: var(--tertiary-color); color: var(--text-dark); padding: 5px 10px; border-radius: 8px; text-transform: uppercase; }

.pokemon-main { text-align: center; position: relative; z-index: 1; }
.pokemon-figure { background-color: rgba(255, 255, 255, 0.1); border-radius: 15px; padding: 10px; margin-bottom: 25px; box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1); }
.pokemon-image { width: 80%; max-width: 200px; margin-bottom: 0; }

.pokemon-stats { display: grid; grid-template-columns: 1fr 1fr; gap: 20px; text-align: left; background-color: rgba(0, 0, 0, 0.2); border-radius: 10px; padding: 15px; }
.stat-item { display: flex; flex-direction: column; }
.stat-item .label { font-size: 0.85em; color: var(--text-light); opacity: 0.7; }
.stat-item .value { font-size: 1.1em; font-weight: bold; color: var(--text-light); }

.pokedex-footer { border-top: 2px dashed var(--border-color); padding-top: 20px; margin-top: 30px; text-align: center; position: relative; z-index: 1; }
.pokedex-footer h2 { margin: 0 0 20px 0; font-size: 1.1em; color: var(--text-light); opacity: 0.8; }
.other-pokemons { display: grid; grid-template-columns: repeat(4, 1fr); gap: 12px; }
.pokemon-button { background: rgba(255, 255, 255, 0.15); border: none; border-radius: 12px; padding: 10px; cursor: pointer; transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease; box-shadow: 0 4px 8px rgba(0,0,0,0.2);} 
.pokemon-button img { width: 100%; filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.5)); }
.pokemon-button:hover { transform: translateY(-5px) scale(1.05); box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3), 0 0 10px var(--tertiary-color); background-color: rgba(255, 255, 255, 0.3);} 

@media (min-width: 768px) {
    .pokedex { max-width: 750px; padding: 35px; }
    .pokedex-header h1 { font-size: 2.5em; }
    .pokedex-brand { font-size: 1em; padding: 7px 12px; }
    .pokemon-main { display: grid; grid-template-columns: 1fr 1.2fr; align-items: center; gap: 40px; }
    .pokemon-figure { padding: 15px; }
    .pokemon-image { max-width: 300px; }
    .pokemon-stats { grid-template-columns: 1fr 1fr; gap: 25px; padding: 20px; }
    .stat-item .label { font-size: 0.9em; }
    .stat-item .value { font-size: 1.3em; }
    .pokedex-footer h2 { font-size: 1.2em; }
    .other-pokemons { gap: 15px; }
    .pokemon-button { padding: 12px; }
}
